  article: DetailedArticle
  show: boolean
}

const props = defineProps<Props>()

// Emits
interface Emits {
  'close': []
  'bookmark': [articleId: string]
  'share': [article: DetailedArticle, platform: string]
}

const emit = defineEmits<Emits>()

// å“åº”å¼æ•°æ®
const showShareMenu = ref(false)

// è®¡ç®—å±æ€§
const article = computed(() => props.article)

// äº‹ä»¶å¤„ç†
const handleOverlayClick = () => {
  emit('close')
}

const handleImageError = (event: Event) => {
  const target = event.target as HTMLImageElement
  target.src = '/images/default-article.jpg'
}

const handleBookmark = () => {
  emit('bookmark', props.article.id)
}

const toggleShare = () => {
  showShareMenu.value = !showShareMenu.value
}

const handleShare = (platform: string) => {
  if (platform === 'copy') {
    navigator.clipboard.writeText(window.location.href)
    alert('é“¾æ¥å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
  }
  emit('share', props.article, platform)
  showShareMenu.value = false
}

const filterByTag = (tag: string) => {
  console.log('æŒ‰æ ‡ç­¾ç­›é€‰:', tag)
  // è¿™é‡Œå¯ä»¥å®ç°æ ‡ç­¾ç­›é€‰åŠŸèƒ½
}

const formatDate = (date?: string | Date) => {
  if (!date) return 'æœªçŸ¥'
  const value = typeof date === 'string' ? new Date(date) : date
  const now = new Date()
  const diffTime = Math.abs(now.getTime() - value.getTime())
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
  
  if (diffDays === 1) {
    return 'æ˜¨å¤©'
  } else if (diffDays < 7) {
    return `${diffDays} å¤©å‰`
  } else if (diffDays < 30) {
    const weeks = Math.floor(diffDays / 7)
    return `${weeks} å‘¨å‰`
  } else {
    return value.toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }
}

const formatNumber = (num?: number) => {
  if (typeof num !== 'number') {
    return '0'
  }
  if (num >= 1000) {
    return (num / 1000).toFixed(1) + 'k'
  }
  return num.toString()
}

const getCategoryIcon = (category: string) => {
  const iconMap: Record<string, Component> = {
    'æŠ€æœ¯æ·±åº¦': CodeIcon,
    'DeFiåè®®': CoinsIcon,
    'æ™ºèƒ½åˆçº¦': FileTextIcon,
    'å¼€å‘å®è·µ': ToolIcon,
    'è¡Œä¸šåˆ†æ': TrendingUpIcon,
    'åŒºå—é“¾åŸºç¡€': LayersIcon,
    'å…±è¯†ç®—æ³•': GitBranchIcon,
    'é¡¹ç›®åˆ†äº«': FolderIcon
  }
  return iconMap[category] || FileTextIcon
}

const getDifficultyLabel = (difficulty?: string) => {
  const labelMap: Record<string, string> = {
    'beginner': 'å…¥é—¨',
    'intermediate': 'ä¸­çº§',
    'advanced': 'é«˜çº§',
    'expert': 'ä¸“å®¶'
  }
  return labelMap[difficulty || 'intermediate'] || 'ä¸­çº§'
}

// ç‚¹å‡»å¤–éƒ¨å…³é—­åˆ†äº«èœå•
